import { lazy } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import {
  FormOutlined, OrderedListOutlined, PlusCircleOutlined, RightCircleOutlined,
} from '@ant-design/icons';
import { routerPath } from './constant';
