  path: string;
  exact?: boolean;
  /**
   * true
   * 
   * false
   */
  showInSider?: boolean;
  /**
   * 
   */
  Component: React.ComponentType<any>;
  /**
   * 
   * 
   * :
   * 1 store
   * 2 
   */
